.page-main__title {
  .oswald-regular;
  font-size: 36px;
  line-height: 36px;
  color: @basic-black;
  .indent-reset;
  margin-top: 27px;

  @media (min-width: @tablet-width) {
    margin-top: 58px;
    font-size: 60px;
    line-height: 60px;
  }

  @media (min-width: @desktop-width) {
    margin-top: 74px;
  }
}

.page-main__content {
  text-align: left;
  .oswald-regular;
  .indent-reset;
  color: @basic-black;
  text-transform: uppercase;
  margin-bottom: 38px;
  margin-top: 37px;

  @media (min-width: @tablet-width) {
    .set-font-size-line-height(20px; 30px);
    margin-bottom: 66px;
    margin-top: 54px;
  }
}
